< <template>
  <div>
     <div v-if="state" class="div">
      <el-form
        :label-position="labelPosition"
        label-width="80px"
        :model="formLabelAlign"
        class="div1"
      >
        <el-form-item label="商品Id：" label-width="100px" >
          <el-input v-model="formLabelAlign.id" disabled></el-input>
        </el-form-item> 
        <el-form-item label="商品种类：" label-width="100px">
          <el-input v-model="formLabelAlign.species"></el-input>
        </el-form-item>
        <el-form-item label="月销量：" label-width="100px">
          <el-input v-model="formLabelAlign.sole" ></el-input>
        </el-form-item>
        <el-form-item label="净收入：" label-width="100px">
          <el-input v-model="formLabelAlign.income"></el-input>
        </el-form-item>
         <div class="butcal">
            <el-button type="primary" @click="query()">确定</el-button>
            <el-button @click="cancel()">取消</el-button>
         </div>
      </el-form>
    </div>
  <el-row class="userIndexBtn">
    <el-button type="primary" @click="add()">新增</el-button>
    <el-button type="success">修改</el-button>
    <el-button type="danger">删除</el-button>
  </el-row>

  <el-table class="userIndex"  :data="tableData">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column prop="id" label="商品Id" width="80px">
    </el-table-column>
    <el-table-column prop="species" label="商品种类" width="110px">
    </el-table-column>
    <el-table-column prop="sole" label="月销量" width="110px">
    </el-table-column>
    <el-table-column prop="income" label="净收入" width="100px">
    </el-table-column>
    <el-table-column label="操作" style="margin-left:20px">
      <template slot-scope="scope">
        <el-button 
          size="mini"
          @click="handleEdit(scope.$index,tableData)"
          type="success"
          >编辑</el-button
        >
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button
        >
      </template>
    </el-table-column>
  </el-table>
 </div>
</template> 




<script>
export default {
  data() {
    return {
      value: true,
      state: false,
      tableData: [],
      labelPosition: "left",
      formLabelAlign: {},
      index:''
    };
  },
  methods: {
    flowerNumber(){
        this.$http.get("/number/owners/").then(res=>{
            this.tableData=res.data
            console.log(res.data);
        })
    },
    handleEdit(index,tableData) {
      this.state = true;
      this.index = index
      // console.log(index,tableData);
      this.formLabelAlign=this.tableData[index]
      console.log(this.formLabelAlign);
    },
    query(){
      // console.log(this.formLabelAlign.sole,);
        this.$http({url:'/number/owners/',method:"put",
        data:{
          id:this.formLabelAlign.id,
          sole:this.formLabelAlign.sole,
          species:this.formLabelAlign.species,
          income:this.formLabelAlign.income,
        }}
        ).then(res=>{ 
           this.state = false
          if (res.data.code == 1) return this.$message.success(res.data.msg); 
      
        }).catch(e=>{
          this.$message.error(res.data.msg);
        })
    },
    cancel(){
      this.state = false
    },
    handleDelete(index, row) {
        console.log(row)
        this.$http({url: '/number/owners/',method:"delete",data:{id:row.id}}).then(res=>{ 
             this.tableData.splice(index,1)
        })
    },
  },
  mounted(){
      this.flowerNumber()
  }
};
</script>

<style scoped>
.userIndexBtn {
  width: 100%;
  height: 100%;
  border-radius: 6px 6px 0 0;
  background-color: #fff;
  padding: 20px 20px;
  border-bottom: 1px solid rgb(239, 238, 238);
}
.userIndex {
  width: 100%;
  height: 100%;
  border-radius: 0 0 6px 6px;
}
.el-icon-time:before {
  content: "";
}
.div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9;
}
.div1 {
 width: 500px;
 padding:30px 30px;
 background-color: #fff;
 position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 5px;
}
.butcal{
  display: flex;
  justify-content: flex-end;
}
</style>